<template>
	<section id="pricing-plan">
		<div v-show="show" style="height: 70vh;line-height: 70vh;" align="center">
			<b-spinner type="grow" label="Loading..." />
		</div>
		<div v-show="!show">
			<!-- title text and switch button -->
			<div class="text-center">
				<h1 class="mt-1">
					{{ !RenewId ? '选择您的订阅' : '选择您要续费的周期进行续费' }}
				</h1>
				<p class="mb-2 pb-75">
					您今后可随时更换您的套餐，我们会自动根据您的购买时间及使用量进行折抵。
					<br />购买Standard/Enterprise季付及以上的用户可免费申请Netflix共享账户。
				</p>
				<div class="d-flex align-items-center justify-content-center mb-5 pb-50" v-show="!RenewId">
					<h6 class="mr-1 mb-0">
						按月订阅
					</h6>
					<b-form-checkbox
						id="priceSwitch"
						v-model="status"
						name="price-switch"
						value="annually"
						unchecked-value="monthly"
						switch
						@input="tooglePlan"
					/>
					<h6 class="ml-50 mb-0">
						按量订阅
					</h6>
				</div>
			</div>
			<!--/ title text and switch button -->

			<!-- pricing plan cards -->
			<b-row class="pricing-card">
				<b-col offset-sm-2 sm="12" md="12" offset-lg="2" lg="10" class="mx-auto">
					<b-row style="justify-content: flex-start">
						<b-col
							:lg="monthlyPlanShow ? 4 : 6"
							v-for="(plans, index) in plan"
							:key="index"
							v-show="monthlyPlanShow ? !plans.onetime_price : plans.onetime_price"
							class="allhover mx-auto"
						>
							<b-card @click="SelectPlan(plans)" :class="{ popular: selectPlan.id == plans.id }" align="center">
								<div class="pricing-badge text-right">
									<b-badge :variant="'light-' + (plans.id == userSub.plan_id ? 'primary' : 'success')">
										{{ plans.id == userSub.plan_id ? '当前订阅 - 可续费' : '可变更' }}
									</b-badge>
								</div>
								<!-- img -->
								<!-- <b-img :src="require('@/assets/images/illustration/' + plans.name + '.svg')" class="mb-2 mt-5" alt="basic svg img" /> -->
								<!--/ img -->
								<h3>{{ plans.name }}</h3>
								<!-- <b-card-text>
								{{ pricing.basicPlan.subtitle }}
							</b-card-text> -->

								<!-- annual plan -->
								<div class="annual-plan">
									<div class="plan-price mt-2">
										<sup class="font-medium-1 font-weight-bold text-primary">平均</sup>
										<span class="pricing-basic-value font-weight-bolder text-primary">
											{{ MonthPrice(plans) }}
										</span>
										<sub class="pricing-duration text-body font-medium-1 font-weight-bold">/{{ monthlyPlanShow ? '月' : '一次性' }}</sub>
									</div>
								</div>
								<!--/ annual plan -->

								<!-- plan benefit -->
								<b-list-group class="list-group-circle text-left" v-html="plans.content"> </b-list-group>
								<!--/ plan benefit -->
							</b-card>
						</b-col>
					</b-row>
				</b-col>
			</b-row>
			<!--/ pricing plan cards -->
			<b-row class="mt-2">
				<b-col lg="10" align="right" class="mx-auto mb-6">
					<b-row>
						<b-col lg="6" align="left" style="align-items: center;display: flex">
							选择您的订阅周期：
						</b-col>
						<b-col lg="6" align="right">
							<b-button
								v-for="buttuns in SelectData"
								:key="buttuns.key"
								@click="SelectMonth(buttuns)"
								v-show="selectPlan[buttuns.key]"
								v-ripple.400="'rgba(113, 102, 240, 0.15)'"
								:variant="selectTime.month == buttuns.month ? 'flat-danger' : 'flat-primary'"
								pill
							>
								{{ buttuns.name }}
							</b-button>
						</b-col>
					</b-row>
				</b-col>
				<b-col lg="10" class="mt-3 mx-auto CouponClass">
					<p>使用促销代码：</p>
					<b-input-group class="col-lg-3" align="right">
						<b-form-input v-model="coupondata" placeholder="输入优惠代码" />
						<b-input-group-append>
							<b-button variant="outline-primary" @click="CouponPost()" :disabled="isOrdering">
								验证
							</b-button>
						</b-input-group-append>
					</b-input-group>
					<!-- <b-form-input class="col-lg-2" v-model="coupondata" id="basicInput" placeholder="输入优惠代码" /> -->
				</b-col>
				<b-col lg="10" class="mt-3 mx-auto">
					<div class="PlanSelectCss">
						<span class="priceclass">
							<sup class="font-medium-1 font-weight-bold text-danger">合计 &nbsp;</sup>
							<span class="font-weight-bolder text-danger">{{ TotilPrice() }}</span>
						</span>
						<b-button @click="order" v-ripple.400="'rgba(255, 255, 255, 0.15)'" :disabled="isOrdering" variant="warning">
							<feather-icon icon="ShoppingCartIcon" class="mr-50" />
							<span class="align-middle">购买</span>
						</b-button>
					</div>
				</b-col>
			</b-row>
		</div>
	</section>
</template>

<script>
import {
	BInputGroup,
	BFormInput,
	BInputGroupAppend,
	BButtonGroup,
	BFormCheckbox,
	BRow,
	BCol,
	BCard,
	BImg,
	BCardText,
	BListGroup,
	BListGroupItem,
	BButton,
	BBadge,
	BSpinner
} from 'bootstrap-vue'
import AppCollapse from '@core/components/app-collapse/AppCollapse.vue'
import AppCollapseItem from '@core/components/app-collapse/AppCollapseItem.vue'
import Ripple from 'vue-ripple-directive'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

/* eslint-disable global-require */
export default {
	components: {
		BButtonGroup,
		BFormCheckbox,
		BButton,
		BCardText,
		BListGroup,
		BListGroupItem,
		BRow,
		BCol,
		BCard,
		BBadge,
		BImg,
		BInputGroup,
		BFormInput,
		BInputGroupAppend,
		BSpinner,
		AppCollapseItem,
		AppCollapse,
		ToastificationContent
	},
	directives: {
		Ripple
	},
	data() {
		return {
			couponStatus: false,
			SelectData: [
				{ name: '月付', key: 'month_price', month: 1 },
				{ name: '季付', key: 'quarter_price', month: 3 },
				{ name: '半年付', key: 'half_year_price', month: 6 },
				{ name: '年付', key: 'year_price', month: 12 },
				{ name: '两年付', key: 'two_year_price', month: 24 },
				{ name: '三年付', key: 'three_year_price', month: 36 },
				{ name: '一次性', key: 'onetime_price', month: 0 }
			],
			selectPlan: {},
			selectTime: { name: '季付', key: 'quarter_price', month: 3 },
			plan: [],
			status: 'monthly',
			monthlyPlanShow: true,
			show: true,
			userPlanid: 0,
			coupondata: '',
			coupon: 0,
			isOrdering: false,
			RenewId: null,
			userSub: {}
		}
	},
	watch: {
		// coupondata(newName, oldName) {
		// 	if (newName && newName.length >= 5) {
		// 		this.CouponPost()
		// 	} else {
		// 		this.coupon = 0
		// 		this.coupondata = ''
		// 	}
		// }
	},
	mounted() {
		this.userSub = this.$store.state.userconfig.userSub
		if (this.$route.params.id) this.RenewId = this.$route.params.id
		this.$http({
			url: '/api/v1/user/plan/fetch',
			method: 'GET'
		})
			.then(res => {
				if (this.$route.params.id) {
					let ress = []
					res.data.data.forEach((item, index, arr) => {
						if (item.id == this.$route.params.id) {
							ress.push(item)
						}
					})
					res = ress
				} else {
					res = res.data.data
				}

				if (res) {
					this.plan = res
					this.selectPlan = res[0]
				}
				this.isPosting = false
				this.show = false
			})
			.catch(err => {
				if (err.response && err.response.data.message) {
					this.$cookies.set('isLogin', 0)
					this.$router.push({
						name: 'Login'
					})
				}
				this.isPosting = false
			})

		this.$http({
			url: '/api/v1/user/getStat',
			method: 'GET'
		})
			.then(res => {
				res = res.data
				if (res) {
					this.userPlanid = Math.max.apply(null, res.data)
				}
			})
			.catch(err => {
				if (err.response.data.message) {
					this.$cookies.set('isLogin', 0)
					this.$router.push({
						name: 'Login'
					})
				}
			})
	},
	created() {},
	methods: {
		order() {
			if (!this.isOrdering) {
				let data = {
					plan_id: this.selectPlan.id,
					cycle: this.selectTime.key
				}
				if (this.coupondata && this.coupon > 0) data.coupon_code = this.coupondata
				this.isOrdering = true
				this.$http({
					url: '/api/v1/user/order/save',
					method: 'POST',
					data: data
				})
					.then(res => {
						res = res.data.data
						if (res) {
							this.$router.push({
								name: 'payorder',
								params: { id: res }
							})
						}
						this.isOrdering = false
					})
					.catch(err => {
						if (err.response.data.message) this.postError = err.response.data.message
						this.$toast({
							component: ToastificationContent,
							props: {
								title: err.response.data.message,
								icon: 'BellIcon'
							}
						})
						this.isOrdering = false
					})
			}
		},
		CouponPost() {
			let data = {
				code: this.coupondata,
				plan_id: this.selectPlan.id
			}
			if (this.coupondata && this.coupondata.length >= 5) {
				this.isOrdering = true
				this.$http({
					url: '/api/v1/user/coupon/check',
					method: 'POST',
					data: data
				})
					.then(res => {
						this.$toast({
							component: ToastificationContent,
							props: {
								title: '验证成功',
								icon: 'BellIcon'
							}
						})
						res = res.data.data
						//console.log(res)
						this.coupon = res.value
						this.isOrdering = false
					})
					.catch(err => {
						if (err.response.data.message) {
							this.$toast({
								component: ToastificationContent,
								props: {
									title: '优惠券错误',
									icon: 'BellIcon'
								}
							})
							this.isOrdering = false
							this.is_coupon_valid = false
							this.coupon = 0
							this.coupondata = ''
						}
					})
			}
		},
		tooglePlan() {
			const MonthData = []
			const OneData = []
			this.plan.forEach((item, index, arr) => {
				if (!item.onetime_price) {
					MonthData.push(item)
				} else {
					OneData.push(item)
				}
			})
			if (this.status === 'monthly') {
				this.monthlyPlanShow = true
				this.selectTime = { name: '季付', key: 'quarter_price', month: 3 }
				this.selectPlan = MonthData[1]
			} else {
				this.monthlyPlanShow = false
				this.selectTime = { name: '一次性', key: 'onetime_price', month: 0 }
				this.selectPlan = OneData[0]
			}
		},
		SelectPlan(plan) {
			this.selectPlan = plan
			if (!plan.month_price && this.selectTime.month == 1) this.selectTime = { name: '季付', key: 'quarter_price', month: 3 }
			if (!plan.three_year_price && this.selectTime.month == 36) this.selectTime = { name: '两年付', key: 'two_year_price', month: 24 }
		},
		SelectMonth(btn) {
			this.selectTime = btn
		},
		MonthPrice(data) {
			let coupon = this.coupon / 100
			if (data.onetime_price) {
				return (data.onetime_price / 100) * (1 - coupon)
			}
			if (!data.three_year_price && this.selectTime.month == 36) {
				return Math.round((data.two_year_price / 100 / 24) * (1 - coupon))
			}
			if (!data.month_price && this.selectTime.month == 1) {
				return Math.round((data.quarter_price / 100 / 3) * (1 - coupon))
			} else {
				return Math.round((data[this.selectTime.key] / 100 / this.selectTime.month) * (1 - coupon))
			}
		},
		TotilPrice() {
			let coupon = this.coupon / 100
			let price = (this.selectPlan[this.selectTime.key] / 100) * (1 - coupon)
			return price.toFixed(2)
		}
	}
}
/* eslint-disable global-require */
</script>

